.app {
  width: 100%;
  height: 100%;
}

.nav {
  position: absolute;
  top: 0.5rem;
  right: 1rem;
  width: 20rem;
  display: flex;
  justify-content: space-between;
}

.nav button {
  border: none;
  background-color: transparent;
  font-size: 2rem;
  width: 6rem;
  color: rgb(13, 109, 235);
}

.nav button:hover {
  color:rgb(255, 16, 127);
  text-decoration: underline rgb(255, 16, 127);
}
